<script>
import { dateNew, flexbox, currencyFomate, Spring } from "../common";
import moment from "moment";
import PullTo from "vue-pull-to";
import commDate from "../comm.json";
export default {
  components: {
    PullTo
  },
  data() {
    return {
      abc: "",
      userType: "",
      bedMsg: false,
      isDestinSome: false,
      isPreImg: false,
      isCal: false,
      isMap: false,
      isDistribute: false,
      isRestrictTips: false,
      isRunning: false,
      isloadaddMsg: true,
      msgNum: 0,
      shwoNum: 5,
      commJson: commDate,
      currency: "CNY",
      first: false,
      bookTypeArr: ["空", "订完", "部分可订", "查看", "待确认", "即时确认"],
      hotelstarList: commDate.hotelstarList,
      breakfastNumArr: commDate.breakfastNumArr,
      currencyObj: commDate.currencyList,
      icon_Icon: {
        wifi: "icon-icons-facility01",
        park: "icon-icons-facility03",
        swim: "icon-icons-facility15",
        foot: "icon-icons-facility13",
        baggage: "icon-icons-facility10",
        pickUp: "icon-icons-facility28"
      },
      req_info: {
        checkInDate: moment()
          .add(1, "days")
          .format("YYYY-MM-DD"),
        checkOutDate: moment()
          .add(2, "days")
          .format("YYYY-MM-DD")
      },
      roomNums: "",
      destinSomeArr: [],
      model: "",
      planMsg: "",
      planMsg2: "",
      preImgList: [],
      restrictTipMsg: "",
      destinSomeMsg: "",
      uers: "",
      listflag: false
    };
  },
  filters: {
    dateFilter(date) {
      if (date) return moment(date).format("MM月DD日");
    },
    dateFilter2(date) {
      if (date) return moment(date).format("MM-DD");
    },
    weekFilter(date) {
      var weekArr = ["日", "一", "二", "三", "四", "五", "六"];
      var week = moment(date).format("d");
      return weekArr[week];
    },
    currencyFilter(type) {
      return currencyFomate(type + 1);
    },
    bedTypeFilter(str) {
      var bedTypeList = commDate.bedTypeList;
      var typelist = str.split(",");
      var resultarr = [];
      typelist.forEach(function(val, idx) {
        if (val.length > 0) resultarr.push(bedTypeList[val]);
      });
      return resultarr.join(",");
    },
    breakfastFilter(type) {
      var type = type - 0;
      var breakfastNumArr = commDate.breakfastNumArr;
      return breakfastNumArr[type + 1];
    },
    cancelRestrictTypeFilter(type) {
      var arr = ["不可取消", "限时取消", "限时取消", ""];
      if (type) return arr[type - 1];
    }
  },
  created: function() {
    var _t = this;
    setTimeout(function() {
      _t.first = true;
    }, 1000);
    this.uersInfo();
    this.Qinfo();
  },
  mounted: function() {
    var _t = this;
    F.loading.hide();
    var hFlag = !!this.$route.query.type;
    $(".loadimg")
      .children()
      .hide();
    setTimeout(function() {
      $(".room")
        .eq(0)
        .addClass("show");
    }, 700);
    setTimeout(function() {
      $(".loadimg")
        .children()
        .fadeIn("slow");
      $(".loadimg").removeClass("loadimg");
      _t.roomClick();
    }, 1500);
  },
  methods: {
    refresh(loaded) {
      this.Qinfo(loaded);
      setTimeout(function() {
        //loaded('done');
      }, 300);
    },

    Qinfo(loaded) {
      var _t = this;
      var data = this.req_info;
      _t.listflag = false;
      this.$ajax
        .get("/hotel/commodity/queryRoomList.shtml", { params: data })
        .then(function(res) {
          if (res.data.result == 1) {
            _t.listflag = true;
            setTimeout(function() {
              _t.roomClick();
            }, 300);
            _t.model = res.data.model;
            _t.roomNums = moment(_t.req_info.checkOutDate).diff(
              moment(_t.req_info.checkInDate),
              "days"
            );
            loaded("done");
          }
        })
        .catch(function(err) {});
    },

    uersInfo() {
      var _t = this;
      this.$ajax
        .post("/common/queryUserInfo.shtml")
        .then(function(res) {
          if (res.data.result == 1) {
            var uers = res.data.model;
            _t.userType = res.data.model.userType;
            _t.uers = res.data.model.distributeInfoVO;
            var title = res.data.model.hotelName;
            document.title = title;
            if (uers.userType == 2 && !uers.memberInfoVO.mobile) {
              _t.isDistribute = false;
            }
            if (uers.userType == 1 && uers.distributeInfoVO.phone) {
              _t.isDistribute = true;
            }
          }
        })
        .catch(function(err) {});
    },

    RestrictTips(item) {
      this.isRestrictTips = true;
      this.restrictTipMsg = item;
    },

    datechg(val) {
      this.req_info.checkInDate = val.beginDate;
      this.req_info.checkOutDate = val.endDate;
      this.Qinfo();
    },

    openPreImg(item, idx) {
      var _t = this;
      this.preImgList = item;
      this.isPreImg = true;
      setTimeout(function() {
        var swiper = new Swiper(".swiper-container", {
          spaceBetween: 10,
          loop: true,
          onInit: function(swiper) {
            swiper.slideTo(idx + 1, 0);
            var _this = $(".swiper-slide-active");
            $(".preimgidx").html(swiper.realIndex + 1);
          },
          onSlideChangeEnd: function(swiper) {
            var _this = $(".swiper-slide-active");
            $(".preimgidx").html(swiper.realIndex + 1);
          }
        });
      }, 200);
    },

    roomClick() {
      $(".room").unbind();
      $(".room").click(function() {
        //console.log('click');
        $(this).siblings().removeClass("show");
        //$(this).find('.hotleItem .hotleItem_title').removeClass('dn');
        //$(this).find('.hotleMain').addClass('dn');
        var _height = 0;
        $(this).find(".spec").each(function() {
            _height += $(this).height();
          });
        if ($(".hotleItem_msg_more").length > 0) {
          _height += $(".hotleItem_msg_more").height();
        }
        if ($(this).hasClass("show")) {
          $(this).removeClass("show");
        } else {
          $(this).addClass("show");
          var _this = $(this);
          var _top = _this.offset().top;
        }
      });

      // $('.hotleItem_title_r').unbind();
      // $('.hotleItem_title_r').click(function(){
      //   $(this).parents('.hotleItem').parent().find('.hotleMain').removeClass('dn');
      //   $(this).parent().addClass('dn');
      // })

    },

    ReserveNow(plan, item) {
      //console.log(JSON.stringify(plan));
      //console.log(plan.saleItemDetails[0].quotaNum, plan.saleItemDetails[0].overDraft, '------');
      if (plan.saleItemDetails[0].quotaNum === 0) {
        if (
          plan.saleItemDetails[0].overDraft ||
          plan.saleItemDetails[0].quotaState == 2 ||
          plan.saleItemDetails[0].quotaState == 4
        ) {
          //可超
          plan.saleItemDetails[0].quotaNum = 6;
        } else {
          //不可超
          //no link
        }
      } else if (plan.saleItemDetails[0].quotaNum === "") {
        //没有设置配额
        plan.saleItemDetails[0].quotaNum = 6;
      }
      var queryData = {
        pricePlanId: plan.pricePlanId,
        commodityId: plan.commodityId,
        salePrice: plan.avgPrice,
        checkInDate: this.req_info.checkInDate,
        checkOutDate: this.req_info.checkOutDate,
        roomQuantity: this.roomNums,
        roomTypeName: item.roomTypeName,
        imgUrls: item.imgUrls[0],
        pricePlanName: plan.pricePlanName,
        telePhone: this.model.telePhone,
        roomTypeID: item.roomTypeID,
        firstDayQuotaNum: plan.saleItemDetails[0].quotaNum,
        payMethod: plan.payMethod
      };
      //console.log(JSON.stringify(queryData.firstDayQuotaNum));
      //return
      this.$router.push({
        path: "orderfill",
        query: queryData
      });
    },

    checkMsg(msg, item) {
      this.planMsg = msg;
      this.planMsg2 = item;
      this.bedMsg = true;
    },

    destinSome(item) {
      var begin = 123;
      var end = 123;
      //console.log(this.roomNums, this.req_info.checkInDate, '----');
      this.destinSomeArr = [];
      var i = 0;
      for (; i <= this.roomNums; i++) {
        this.destinSomeArr.push(
          moment(this.req_info.checkInDate)
            .add(i, "days")
            .format("YYYY-MM-DD")
        );
      }
      //console.log(this.destinSomeArr);

      this.destinSomeMsg = item;
      this.isDestinSome = true;
    },

    showMap() {
      var data = {
        lat: this.model.latBaidu,
        lng: this.model.lngBaidu,
        address: this.model.hotelAddress,
        hotelName: this.model.hotelName
      };
      this.$router.push({
        path: "/map",
        query: data
      });
    },

    /*others*/
    blankClick() {},

    dom() {},

    add() {
      var _t = this;
      this.isloadaddMsg = false;
      this.msgNum = this.msgNum + 1;
      setTimeout(function() {
        _t.shwoNum = 5 + _t.msgNum * 10;
        _t.isloadaddMsg = true;
      }, 1000);
    },

    callTel() {
      window.location.href = "tel:" + this.model.telePhone;
    },

    base() {
      var _t = this;
      var data = this.req_login;
      this.$ajax
        .post("", data)
        .then(function(res) {
          if (res.data.result == 1) {
          }
        })
        .catch(function(err) {});
    },
    watch: {
      isCal(val) {
        if (val) {
          $("body").css("overflow-y", "hidden");
        } else {
          $("body").css("overflow-y", "scroll");
        }
      }
    }
  }
};
</script>



